<template>
  <div id="view-task">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{title}}</h4></li>
      <li class="collection-item">Task Id: {{task_id}}</li>
      <li class="collection-item"> Tag: {{tag}}</li>
      <li class="collection-item"> Description: {{description}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteTask" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-task', params: {task_id: task_id}}" class="btn-floating red btn-large">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default{
    name: 'view-task',
    data (){
        return{
            task_id: null,
            description: null,
            tag: null,
            title: null
        }
    },
    beforeRouteEnter(to, from, next){
      db.collection('tasks').where('task_id', '==', to.params.task_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc =>{
              next(vm => {
                vm.task_id = doc.data().task_id
                vm.title = doc.data().title
                vm.description = doc.data().description
                vm.tag = doc.data().tag
              })
            })
        })

    },
    watch:{
        '$route': 'fetchData'
    },
    methods:{
      fetchData(){
        db.collection('tasks').where('task_id', '==', this.$route.params.task_id).get()
          .then( querySnapshot => {
            querySnapshot.forEach(doc => {
                this.task_id = doc.data().task_id;
                this.description = doc.data().description;
                this.tag = doc.data().tag;
                this.title = doc.data().title;
            })
          })
      },
      deleteTask(){
          if(confirm('are you sure?')){
            db.collection('tasks').where('task_id', '==', this.$route.params.task_id).get()
              .then( querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
              })
          }
      },
    }
  }
</script>



