<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Tasks</h4></li>
      <li v-for="task in tasks" v-bind:key="task.id" class="collection-item">
        {{task.title }}

        <router-link class="secondary-content" v-bind:to="{name: 'view-task', params: {task_id: task.task_id}}">
          <i class="fa fa-eye "></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating red btn-large">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default{
    name: 'dashboard',
    data (){
        return{
          tasks: []
        }
    },
    created(){
      db.collection('tasks').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'task_id': doc.data().task_id,
              'description': doc.data().description,
              'tag': doc.data().tag,
              'title': doc.data().title
            }
            this.tasks.push(data)
        })
      })
    }
  }
</script>



