<template>
  <div id="new-task">
    <h3>New task</h3>
    <div class="row">
      <form @submit.prevent="saveTask" class="col s12">
        <div class="row">
          <div class="input-field coll s12">
            <input id="task" type="text" v-model="task_id" required>
            <label for="task">Task id#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <input id="title" type="text" v-model="title" required>
            <label for="title">Task title:</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <input id="flag" type="text" v-model="tag" required>
            <label for="flag">Task flag:</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <textarea name="" id="description" cols="30" rows="110" v-model="description"></textarea>
            <label for="description"></label>
          </div>
        </div>
        <button @click="createNewTask" type="submit" class="btn">Submit</button>
        <button  type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default{
     name: 'new-task',
     data (){
         return{
           task_id: null,
           title: null,
           tag: null,
           description: null
         }
     },
     methods:{
         createNewTask (){
           db.collection('tasks').add({
             task_id: this.task_id,
             title: this.title,
             tag: this.tag,
             description: this.description,
           })
           .then(docRef => {this.$router.push('/')})
           .catch(error => console.log(error))
       }
     }
  }
</script>



