<template>
  <div id="edit-task">
    <h3>Edit task</h3>
    <div class="row">
      <form @submit.prevent="updateTask" class="col s12">
        <div class="row">
          <div class="input-field coll s12">
            <input disabled id="task" type="text" v-model="task_id" required>
            <label for="task">Task id#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <input id="title" type="text" v-model="title" required>
            <label for="title">Task title:</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <input id="flag" type="text" v-model="tag" required>
            <label for="flag">Task flag:</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field coll s12">
            <textarea name="" id="description" cols="30" rows="110" v-model="description"></textarea>
            <label for="description"></label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default{
    name: 'edit-task',
    data (){
      return{
        task_id: null,
        title: null,
        tag: null,
        description: null
      }
    },
    beforeRouteEnter(to, from, next){
      db.collection('tasks').where('task_id', '==', to.params.task_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc =>{
            next(vm => {
              vm.task_id = doc.data().task_id
              vm.title = doc.data().title
              vm.description = doc.data().description
              vm.tag = doc.data().tag
            })
          })
        })

    },
    watch:{
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('tasks').where('task_id', '==', this.$route.params.task_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.task_id = doc.data().task_id
            this.title = doc.data().title
            this.description = doc.data().description
            this.tag = doc.data().tag
          })
        })
      },
      updateTask () {
        db.collection('tasks').where('task_id', '==', this.$route.params.task_id).get()
          .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref.update({
              task_id: this.task_id,
              title: this.title,
              description: this.description,
              tag: this.tag,
            }).then(() => {
                this.$router.push({ name: 'view-task', params: { task_id: this.task_id }})
              });
          })
        })
      }
    }
  }
</script>




